@media (min-width: 768px) {
  .navbar-brand.abs {
    position: absolute;
    width: 100%;
    left: 0;
    text-align: center;
    margin:0 auto;
  }
}

@media (max-width: 768px) {
  .nav-link {
    padding-left: 10px !important;
  }
}

.navbar-brand.abs img {
  padding: 0 15px;
}

.nav-item img {
  height: 28px;
}

.navbar {
  font-family: 'GreycliffCF';
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  padding: 0.85rem 1rem !important;
  border-bottom: #c90e3a 5px solid;
  line-height: 2;
}

.navbar-collapse {
  z-index: 1;
}

.nav-item a:hover {
  color: #fff !important;
  background-color: #c90e3a;

  -webkit-transition: all 200ms ease-in;

  transition: all 200ms ease-in;
}

.nav-link.active {
  color: #fff !important;
  background-color: #c90e3a;
}

.footer {
  background-color: #ababab;
  text-align: center;
  padding: 50px 0 0;
  margin-top: 50px;
  font-family: 'GreyCliffCF'
}

.social span {
  color: #c90e3a;
}

.social span:hover {
  color: #fff;
}

.social {
  margin-bottom: 50px;
  width: 80%;
}

.contact {
  background-color: #d5d5d5;
  padding: 20px;
}

.contact span {
  margin-bottom: 5px;
}

.contact a {
  color: #c90e3a;
}

.contact a:hover {
  color: #960a2b;
  text-decoration: none;
}

.footerFoot {
  color: #fff;
  background-color: #c90e3a;
  text-align: center;
  padding: 20px;
}

.footerFoot a {
  color: #fff;
}

.footerFoot a:hover {
  color: #ebebeb;
  text-decoration: none;
}

.footer img {
  height: 100px;
  margin: 20px 0;
}

