.z-depth-1 {
  box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 1px 5px 0 rgba(0,0,0,0.12), 0 3px 1px -2px rgba(0,0,0,0.2);
}

.z-depth-2 {
  box-shadow: 0 4px 5px 0 rgba(0,0,0,0.14), 0 1px 10px 0 rgba(0,0,0,0.12), 0 2px 4px -1px rgba(0,0,0,0.3);
}

.z-depth-3 {
  box-shadow: 0 6px 10px 0 rgba(0,0,0,0.14), 0 1px 18px 0 rgba(0,0,0,0.12), 0 3px 5px -1px rgba(0,0,0,0.3);
}

.z-depth-4 {
  box-shadow: 0 8px 10px 1px rgba(0,0,0,0.14), 0 3px 14px 2px rgba(0,0,0,0.12), 0 5px 5px -3px rgba(0,0,0,0.3);
}

.z-depth-5 {
  box-shadow: 0 16px 24px 2px rgba(0,0,0,0.14), 0 6px 30px 5px rgba(0,0,0,0.12), 0 8px 10px -5px rgba(0,0,0,0.3);
}

.bg-img {
  	position: absolute;
    top: 0;
  	left: 0;
  	right: 0;
  	height: 100%;
    z-index: -1;

  	background: -webkit-gradient(
                       linear,
                       left top, left bottom,
                       from(rgba(10,10,10, .5)),
                       to(rgba(10,10,10, .5))),
                       url(/uapsk_master/static/media/bg.779a85be.png) no-repeat center center fixed;

  	background: -webkit-linear-gradient(
                       rgba(10,10,10, .5),
                       rgba(10,10,10, .5)),
                       url(/uapsk_master/static/media/bg.779a85be.png) no-repeat center center fixed;

  	background: linear-gradient(
                       rgba(10,10,10, .5),
                       rgba(10,10,10, .5)),
                       url(/uapsk_master/static/media/bg.779a85be.png) no-repeat center center fixed;

  	background-size: cover;
  	-webkit-filter: blur(10px);
  	        filter: blur(10px);
}

.navbar.fixed-bottom {
  border-bottom: 0;
  border-top: #c90e3a 5px solid;
}

.titlePage {
  margin-top: 15vh;
  text-align: center;
  color: white;
  font-family: 'GreycliffCF';
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.titlePage img {
  max-height: 150px;
  margin-bottom: 20px;
}

.titlePage h1 {
  font-size: 3.5rem;
  font-weight: bold;
}

.titlePage h4 {
  color: #c90e3a;
  font-weight: bold;
  margin-top: 20px;
  -webkit-text-stroke: 1px white;
}

@media (max-width: 568px) {
  .titlePage {
    margin-top: 5vh;
    padding: 10px;
  }

  .titlePage img {
    max-height: 100px;
    margin-bottom: 10px;
  }

  .titlePage h1 {
    font-size: 2.5rem;
    font-weight: bold;
  }
}

.principles h3 {
  color: #c90e3a;
}

.creed {
  background: #c90e3a;
  color: white;
  margin: 30px 0;
  padding: 20px;
}

.brother {
  margin-bottom: 40px;
}

.brother h5 {
  text-transform: uppercase;
  color: #c90e3a;
  font-weight: bold;
}

.brother .crop {
    height: 250px;
    width: 200px;
    overflow: hidden;
    margin: 0 auto;
    border-radius: 50%;
    margin-bottom: 10px;
}

.brother .crop img {
    height: 100%;
}

.blank img {
    margin-left: -20px;
}

.brothers > .col-md-4:nth-child(3n+1) {
    clear: both;
}

.event {
  background: #fff;
  padding: 30px 0;
  margin: 30px auto;
  font-family: 'GreyCliffCF';
}

.event.alt {
  background: #c90e3a;
  color: #fff;
}

.eventDate {
  text-align: center;
  border-right: #c90e3a 5px solid;
}

.event.alt .eventDate {
  border-right: #fff 5px solid;
}

.eventDate h1 {
  font-weight: bold;
  font-size: 3.5em;
}

.eventDate h2 {
  text-transform: uppercase;
  font-weight: bold;
}

.eventDate h5 {
  margin-top: 15px;
  text-transform: uppercase;
}

.eventDetails {
  padding: 50px;
}

@media (max-width: 768px) {
  .eventDetails {
    text-align: center;
  }

  .eventDate {
    border-right: 0;
  }
}

.eventDetails h2 {
  font-weight: bold;
  text-transform: uppercase;
}

.eventDetails h4 {
  font-weight: bold;
}

.eventDetails i {
  padding-right: 10px;
}


.award {
  background: #fff;
  padding: 30px 10px;
  font-family: 'GreyCliffCF';
}

.award.alt {
  background: #c90e3a;
  color: #fff;
}

.award h1, h2 {
  text-transform: uppercase;
}

.award h1 {
  font-weight: bold;
}

.awardCol {
  padding: 10px;
}

.letters {
  width: 100%;
}

.awards {
  margin: 5px;
  text-align: center;
}

